		uniLoadMore,
		tuiTabs
	},
	onNavigationBarButtonTap() {
		uni.navigateTo({
			url:'issuesupply'
		})
	},
	data(){
		return{
			loadingimg:true,//login加载
			loadingType:1,//login状态
			List:[{},{},{},{}],
